export default {
	data() {
		return {
			countName: '',
			password: '',
			passwordConfirm:'',
			vCode:'',
			suggestCode: ''
		}
	},
	methods: {
		getVCode(){
			var that = this
			uni.request({
				url:that.baseUrl + 'login/vcode',
				method:"POST",
				header: {
					'content-type': 'application/x-www-form-urlencoded', 
				},
				data:{
					mobile: that.countName
				},
				success(res) {
					uni.showModal({
						content: res.data.msg,
						showCancel: false
					});
				}
			})
		},
		goToRegister() {
			uni.redirectTo({
				url: "../loginPage/loginPage"
			})
		},
		registerNow(){
			var that = this
			if(this.suggestCode != '' && this.suggestCode.length != 11){
				uni.showModal({
					content: '请输入11位手机号',
					showCancel: false
				});
				return
			}
			if(this.suggestCode == ''){
				this.suggestCode = '0'
			}
			uni.request({
				url:that.baseUrl + 'login/register',
				method:"POST",
				header: {
					'content-type': 'application/x-www-form-urlencoded', 
				},
				data:{
					mobile:that.countName,
					password1:that.password,
					password2:that.passwordConfirm,
					code:that.vCode,
					suggest: that.suggestCode 
				},
				success(res) {
					if(res.data.code == 1){
						uni.showToast({
							title:res.data.msg,
							icon:'none',
							duration: 1000,
							success() {
								setTimeout(function() {
									//要延时执行的代码
									uni.redirectTo({
										url: '../loginPage/loginPage'
									});
								}, 1000)
							}
						})
						
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none',
							duration: 2000
						})
					}
				}
			})
		}
	}
}
